import Box from '@mui/material/Box';
import Collapse from '@mui/material/Collapse';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import { useState } from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import { Link } from 'react-router-dom';
import { Button } from '@material-ui/core';
function getavg(row){
        let createdgrades=0
        for (let i=0;i<row.length;i++){
         createdgrades=createdgrades+row[i].grade}
         let avg=isNaN(createdgrades/row.length)?0:(createdgrades/row.length).toFixed(2)
         return avg}
function FinishedRow(props){
    const {row ,handleFormDHN } = props;
    const [open, setOpen] =useState(false);
 return( <>
        <TableRow sx={{ '& > *': { borderBottom: 'unset', } }}>
        <TableCell>
          <IconButton
            aria-label="expand row"
            size="small"
            onClick={() => setOpen(!open)}
          >
            {open ? <button className='ctable1'></button> : <button className='ctable'></button>}
          </IconButton>
        </TableCell>
        <TableCell component="th" scope="row">{row[0].creatorname||row[0].username}</TableCell>
        <TableCell align='center'>{row.length}</TableCell>
        <TableCell align='center'>{getavg(row)}</TableCell>
        <TableCell align="right">{row[row.length-1].grade}</TableCell>
      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={5}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box sx={{ margin: 1 }}>
              <Typography variant="h6" gutterBottom component="div">
               Detailes
              </Typography>
              <Table size="small"padding='none' aria-label="purchases">
                <TableHead>
                  <TableRow>
                    <TableCell align='left'>{'Form Name'}</TableCell>
                    <TableCell>{'Grade'}</TableCell>
                    <TableCell align='center'>{'Class Avg'}</TableCell>
                    <TableCell align="right">More..</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                {row.map((res) => (
                    <TableRow key={res.formName}>
                      <TableCell component="th" scope="row">
                                  {res.formName}
                      </TableCell>
                      <TableCell align='center'>{res.grade}</TableCell>
                      <TableCell align='center'>{getavg(res.finished)}</TableCell>
                      <TableCell align="right">
                        <Link to='/formdisplay'>
                            <Button size='small' variant="contained" color="primary" onClick={()=>handleFormDHN(res)}>
                                Display
                            </Button>
                        </Link>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </>
  );}
export default FinishedRow