import { useContext } from "react"
import { FormsContext } from "../ContextCreators/FormsContextCreator";
import { groupBy } from '../ProtFunc'
import { Link } from "react-router-dom";
import { Paper , Typography} from "@material-ui/core";
Array.prototype.insert = function ( index, item ) {
        this.splice( index, 0, item );
    };
function SurveyStats(){
    const {surveystats} = useContext(FormsContext)
    let concated = surveystats.questions.concat(surveystats.finished)
    let quest = []
    let answers = []
    for (let i=0;i<concated.length;i++){

       if (Object.keys(concated[i]).length!=4){
            Object.values(concated[i]).map((item)=>{if (typeof(item)=='object'){answers.push(item)}})
            }
        else{
            quest.push(Object.values(concated[i]))
    }}
       let grouped2 = []
       for (let i=0;i<answers.length;i++){
            grouped2.push(...answers[i])
        }
       let stats = []
       for (let i=0;i<grouped2.length;i++){
            stats.push([grouped2[i].substring(0,4),grouped2[i].slice(5,-2)]) 
        }
        let groupedstats = Object.values(groupBy(stats,[1]))
        let htmlitems = []
        let singelanswer = groupedstats.map((item)=>[...item[0],item.length])
        let QandA = Object.values(groupBy((singelanswer.concat(quest)).sort(),[0]))
        for (let i=0;i<QandA.length;i++){
            let sortedqa = []
            QandA[i].map((item)=>{
                typeof(item[item.length-1])==='string'?
                    sortedqa.insert(0,<><Typography variant='h5'>{item[1]}</Typography><br/><Typography variant='h6'> {item[2]}</Typography></>)
                    :
                    sortedqa.push(<Paper sx={{ marginTop : '10px' }}> Answer : {item[1]}<br/> Number of Voters :  {item[2]}</Paper>)})
            htmlitems.push(...sortedqa)    
        }       
return (<>
    <div>
        <Paper>
            <Typography variant='h4'>
                {surveystats.formName}
            </Typography>
            <Typography varinat='h5'>
                {surveystats.formDescription}
            </Typography>
        </Paper>
        {htmlitems}
    </div>
    <div className='survey'>
    <Link to='/home'>
        <button className='but'>
            To Home Page
        </button>
    </Link>
    </div>
    </>
)}
export default SurveyStats