import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { MenuItem } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import { Paper } from '@material-ui/core';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import clsx from 'clsx';
import Tooltip from '@material-ui/core/Tooltip';
import { Link } from 'react-router-dom';
import Title from '../../Title';
import { useState } from 'react';
import {request as axios}  from '../../Axios'
import { useContext } from 'react';
import { FormsContext } from '../../ContextCreators/FormsContextCreator';
import { UserContext } from '../../ContextCreators/UserContextCreator';
const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  absolute: {
    position: 'relative',
    bottom: theme.spacing(3),
    left: theme.spacing(15),
  },
  paper: {
        padding: theme.spacing(2),
        display: 'flex',
        overflow: 'auto',
        flexDirection: 'column',
        margin:'7px'
  },
   fixedHeight: {
    height: 'auto',
    width:'auto',
    
  
  }, 
  inp: {
    marginTop: 9
  },
  
}));

function TestCreation() {
  const classes = useStyles();
  const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);
  const [name, setName] = useState('')
  const [description, setDescription] = useState('')
  const [type, setType] = useState('')
  const [assignment, setAssignment] = useState('')
  const { setTestId , setFormType } = useContext(FormsContext)
  const {userD} = useContext(UserContext)
  async function HandleSubmit() {
    try {
        setFormType(type)
      const data = {"formName": name, "formDescription": description, "creator": userD._id,'creatorname':userD.userName,  'formType': type, 'open': assignment }
        let res = await axios.post('/fcreate', data)
        setTestId(res.data)  
        }
    catch (error) {
      console.error(error)
    }}
    
  return (

        <Paper className={fixedHeightPaper}>
          <form id='form1' className={classes.root} noValidate autoComplete="off">
            <Title>Create a new Form!</Title>
            <p>hello, {userD.userName}<br/>
            create your form here.<br/>
            choose a form name,description,type and if it's visible to everyone or only for custom users.<br/>
            Create a question then create answers for it,
            at the end of the creation you can edit/delete your questions and answers.<br/>
            Also , at the end of the creation you could add users who should do the test and they will be informed
            and add your test deadline.<br/>
            GOODLUCK :)
            </p>
            <TextField onChange={e => setName(e.target.value)} className={classes.inp}type='string' label="Form Name" variant="outlined" required/>
            <TextField onChange={e => setDescription(e.target.value)} className={classes.inp} label="Form Description" variant="outlined" required/>
            <TextField onChange={e => setType(e.target.value)}  className={classes.inp}  id="select1" label="type" select required>
              <MenuItem value="survey">Survey</MenuItem>
              <MenuItem value="test">Test</MenuItem>
            </TextField>
            <TextField onChange={e => setAssignment(e.target.value)} className={classes.inp} value={assignment} id="select2" label="Visible" select required>
              <MenuItem value="true" >All Users</MenuItem>
              <MenuItem value="false">Private</MenuItem>
            </TextField>
            
            <Link to="/QuestionCreation"><Tooltip title="Add" aria-label="add">
              <Fab color="default" className={classes.absolute} onClick={HandleSubmit}>
                <AddIcon />
              </Fab>
            </Tooltip></Link>
          </form>
        
        </Paper>
     );
}

export default TestCreation



