function EditFormDetailes({name,description,deadline}){
    return(<>
    <h4>Great! Youve finished creating your form. take a look that everything is ok before we procced.
                                          <br></br>                                     
            Dont forget to add users in the sent to section above and define a deadline below.<br></br>
            </h4>
             <h3>Form Name:</h3>
    <input defaultValue={name} type='text' name='formName'></input>
            <h3>Form Description:</h3>
    <textarea defaultValue={description} type='text' name='formDescription'></textarea>
            <h3>Form DeadLine:</h3>
    <input value={deadline} type='date' name='deadline'></input></>
)
    }
export default EditFormDetailes