import Chart from "../Chart";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core";
import { Grid } from "@material-ui/core";
import { Paper } from "@material-ui/core";
import Orders from "../Orders";
import { useContext } from "react";
import { FormsContext } from "../../ContextCreators/FormsContextCreator";
import { UserContext } from "../../ContextCreators/UserContextCreator";
import { groupBy } from "../../ProtFunc";
import Deposits from "../Deposits";
import CollapseTable from '../CollapseTable'
const useStyles = makeStyles((theme) => ({
 

     paper: {
        padding: theme.spacing(1),
        display: 'flex',
        overflow: 'auto',
        flexDirection: 'column',
      },
    fixedHeight: {
     height: '300px',
     width:'94%'
  },
      
    }));
function DoneForms(){
    const classes = useStyles();
    const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);
    const {userD} = useContext(UserContext)
    const {doneforms} = useContext(FormsContext)
    const proto=doneforms.map((x)=>x)
    const protarr=[]
    const data=[]
    let prot2=Object.values(groupBy(proto,'creator'))
    for(let i=0;i<prot2.length;i++){
        prot2[i].map((item)=>{
            item.finished.map((i)=>{
                if (i.tested==userD._id){
                    protarr.push(Object.fromEntries(Object.entries(item).concat(Object.entries(i))))}})})}
    let grouped=Object.values(groupBy(protarr,'creator'))
    for (let i=0;i<doneforms.length;i++){
        if (doneforms[i].formType!=='survey'){
        data.push(doneforms[i].creationDate.slice(8,10)+'-'+doneforms[i].creationDate.slice(5,7))
        for (let j=0;j<doneforms[i].finished.length;j++){
            if (doneforms[i].finished[j].tested==userD._id){
                data.push(doneforms[i].finished[j].grade)
                break}}}}
return <>
    <Grid container spacing={1}>
        <Grid item xs={12} md={12} lg={9}>
            <Paper className={fixedHeightPaper}>
                 <Chart data={data}/>
            </Paper>
        </Grid>
        <Grid item xs={12} md={6} lg={3}>
            <Paper className={classes.paper}>
                <CollapseTable forms={grouped} />
            </Paper>
        </Grid>
        <Grid item xs={12} md={6} lg={3}>
            <Paper className={classes.paper}>
                <Deposits />
            </Paper>
        </Grid>
   </Grid>
    </>
}
export default DoneForms