import { useContext } from "react"
import { UserContext } from "../ContextCreators/UserContextCreator"
import { Button } from "@material-ui/core"
import {request as axios}  from '../Axios'
import { useHistory } from "react-router-dom"
function LogOutButton(){
    const {setUser,userD,setUserD} = useContext(UserContext)
    let history=useHistory()
    async function handleLogOut(e){
        e.preventDefault()
		try {
			const data = {'_id':userD._id}
			await axios.post('/logout',data)
            history.push('/')
            setUserD('')
            setUser(false)}
		catch (error){
			console.error(error)
		}}

return                  <Button
                            variant="contained"
                            size="large"
                            onClick={handleLogOut}
                        >
                            LogOut
                        </Button>                  
}
export default LogOutButton