import React, { useContext } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import { Grid, Paper } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import SaveIcon from '@material-ui/icons/Save';
import clsx from 'clsx';
import { Link } from 'react-router-dom';
import { useState } from 'react';
import {request as axios}  from '../../Axios'
import { FormsContext } from '../../ContextCreators/FormsContextCreator';
import { DisclosureContent } from "reakit";




const useStyles = makeStyles((theme) => ({
    route: {
        display: 'flex',
    },
    absolute: {
        position: 'relative',
        bottom: theme.spacing(0),
        left: theme.spacing(13),
    },
    button: {
        margin: theme.spacing(1),
    },
    inp: {
        marginTop: 7,

    },
    paper: {
        padding: theme.spacing(1),
        display: 'flex',
        overflow: 'hidden',
        flexDirection: 'column',
        
    },
    
        fixedHeight: {
    height: 'auto',
    width:'auto',
    minWidth:'280px',
    margin:'2%'
    },
}));

function CreateAnswer() {
    const classes = useStyles();
    const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);
    const [text,setText] = useState('')
    const [points,setPoints] = useState(0)
    const [sent,setSent] = useState(false)
    const [visible, setVisible] = useState(false);
    const {questionid,testid,setAnswerPoints,answerpoints,formItems,qascore,setQascore,formtype} =useContext(FormsContext)
    const id = { "_id": testid } 
     
    async function HandleSubmit() {
        try {
            if (qascore>formItems){
                throw new Error ('answer points is higher then question score.')}
            else{
                const data = [id,'answers',{'Q_id': questionid, "text": text,  "points":points }]
                await axios.post('/qacreate', data)
                setAnswerPoints(answerpoints+Number(points))
                setQascore(qascore+Number(points))
                setSent(true)
                setPoints(0)}}
        catch (error) {
            console.error(error)
            setVisible(!visible)}}
        return (
            <Grid item xs={11} sm={6} md={4}>
                <Paper className={fixedHeightPaper}>
                    <form id={testid} noValidate autoComplete="off">
                        <TextField onChange={e=>setText(e.target.value)} className={classes.inp} label="Answer" variant="outlined" required disabled={sent}/>
                        <label for="points">Score:{points}</label>
                        <input onChange={e=>setPoints(e.target.value)}value={points} type="range" id="range" name="points" min="0" max="100" step="5" disabled={formtype==='survey'||sent?true:false}/>
                        <Link to="/QuestionCreation"><Button
                            variant="contained"
                            color="primary"
                            size="small"
                            onClick={HandleSubmit}
                            className={classes.button}
                            startIcon={<SaveIcon />}
                            disabled={sent}
                        >
                            Save Answer!
                        </Button></Link>
                <DisclosureContent visible={visible}>Not a vaild answers points.</DisclosureContent>
                    </form>
                </Paper></Grid>
           );
}
export default CreateAnswer
