import SocialButtons from "../SocialButtons"
import {request} from '../../Axios'
import { useState,useEffect } from "react"
import { useContext } from "react"
import {useHistory , Link} from 'react-router-dom'
import { UserContext } from "../../ContextCreators/UserContextCreator"
import {FormsContext} from '../../ContextCreators/FormsContextCreator'
import { DisclosureContent } from "reakit";
function Login(){
	const {setUser,setUserD} = useContext(UserContext)
    const {handleFetch} = useContext(FormsContext)
	const [email,setEmail] = useState()
    const [stay,setStay] = useState(false)
    const [visible, setVisible] = useState(false);
    let history = useHistory()
    const [password,setPassword] = useState()
	useEffect(async ()=>{
     try{
            const res = await request.post('/cookie')
            if (res.data!='Invaild Token'){
            history.push('/home')
            setUserD(res.data)
            setUser(true)
            handleFetch(res.data._id,res.data.Email)}
            else {
                throw new Error ('No Token')}}
        catch(e){console.error(e)}},[]) 
	async function handlesubmit(e){
		e.preventDefault()
		try {
			const data = {"Email":email,"password":password,'stay':stay}
			await request.post('/login',data)
			.then((res)=>{
            if (!res.data._id){
            throw new Error('invaild password or email!')}
            if(res.data.Email==email){
            history.push('/home')
            setUserD(res.data)
            setUser(true)
            handleFetch(res.data._id,res.data.Email)}
            })

		   }
		catch (error){
			console.error(error)
            setVisible(!visible)
		}}
   
    return(
       
            <form>
        <h1>Sign in</h1>
        <SocialButtons />
        <span>or use your account</span>
			<input onChange={(e)=>setEmail(e.target.value)}value={email} name="_id" type="email" placeholder="E-Mail" required/>
			<input onChange={(e)=>setPassword(e.target.value)}value={password} name="password" type="password" placeholder="Password" required />
            <label for='stay'>Stay Connected?</label>
            <input onChange={()=>setStay(!stay)}value={stay} type='checkbox' name='stay'/>
			    <Link to='signup'><p>Needs To SIGNUP?</p></Link> 
			<button onClick={(e)=>handlesubmit(e)}>Sign In</button>
    <DisclosureContent visible={visible}>Wrong Email or Password.</DisclosureContent>

		</form>
	
    )}
export default Login