import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import { useContext } from 'react';
import { FormsContext } from '../../ContextCreators/FormsContextCreator';
import { UserContext } from '../../ContextCreators/UserContextCreator';
import Row from './Row';
import { Link } from 'react-router-dom';
import Title from '../../Title';
import FinishedRow  from './FinishedRow';
export default function CollapsibleTable({forms}) {
    const {setTestId , setFormDHN} = useContext(FormsContext)
    const {userD } = useContext(UserContext)
    function handleFormDHN(form){
        let tested1;
        for (let i=0;i<form.finished.length;i++){
             if (form.finished[i].tested==userD._id){
                 tested1=Object.entries(form.finished[i])}}
                 setFormDHN([form.formName,form.formDescription,form.questions,tested1])}
    let chooser;
    for(let i=0;i<forms.length;i++){
        if (forms[i].creator==userD._id){
             chooser='created'
             break}
        else{
             chooser='finished'}} 
  return (
    <TableContainer>
        <Title>Tests Statistics</Title>
      <Table size='small' padding='none' aria-label="collapsible table">
        <TableHead>
          <TableRow>
            <TableCell />
            <TableCell>{chooser=='created'?'Test Name':'Teacher'}</TableCell>
            <TableCell align='center'>{chooser=='created'?'Class Avg':'Quantity'}</TableCell>
            <TableCell align='center'>{chooser=='created'?'Dead Line':'My Average'}</TableCell>
            <TableCell align="right">{chooser=='created'?'CLICK':"Last Grade"}</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {chooser=='finished'?forms.map((row) => (
               <FinishedRow  handleFormDHN={handleFormDHN} row={row} />
          )):forms.map((row)=>
               <Row setFormDHN={setFormDHN} setTestId={setTestId} row={row} /> 
        )}</TableBody>
      </Table>
       <div>
        <Link  to="/TestCreation" >
          Create a New Form!
        </Link>
       </div>
    </TableContainer>
  );
}