import {Paper , Typography} from '@material-ui/core'
import { makeStyles } from '@material-ui/core';
import { useContext } from 'react';
import { FormsContext } from '../ContextCreators/FormsContextCreator';
import { Link } from 'react-router-dom';
import { groupBy } from '../ProtFunc';
import QuestionProt from './QuestionProt';
import DisplayAnswer from './DisplayAnswer';
const useStyles = makeStyles({
  root: {
    padding:'8px',
    width: '100%',
    marginBottom:'10px',
  },
    answer:{
    margin:'20px',
    width:'90%',
    padding:'5px'}
 
});
function FormDisplay(){
    const classes = useStyles();
    const {formDHN} = useContext(FormsContext)
    const question=formDHN.map((x)=>x)
    let concated2=[]
    let detailes=[]
    let proto=question[2].concat(question[3])
    const type = formDHN[2][0].score=='0'?'survey':'test'
    for (let i=0;i<proto.length;i++){
        if(proto[i].Q_id){
            concated2.push(proto[i])}
        else if(proto[i][0]!='grade'&&proto[i][0]!='tested'&&proto[i][0]!='username'&&proto[i][0]!='donedate')
            {concated2.push({"Q_id":proto[i][0],"answers":proto[i][1]})}
        else{
            detailes.push(proto[i])}}
    let grouped=groupBy(concated2,'Q_id')
    let grouped2=Object.values(grouped)
    let prothtml=[]
    let fullytrue=0
    grouped2.map((item)=>{
        prothtml.push(<QuestionProt type={type} name={item[0].name} description={item[0].description} score={item[0].score}/>)
            fullytrue=0
        for (let i=0;i<item[1].answers.length;i++){
            if (!isNaN(Number(item[1].answers[i].slice(-2)))){
                fullytrue=fullytrue+Number(item[1].answers[i].slice(-2))}
            else {
                 fullytrue=fullytrue+Number(item[1].answers[i].slice(-1))}}
        for (let j=0;j<item[1].answers.length;j++){
            fullytrue==item[0].score?
                prothtml.push(
                <DisplayAnswer type={type} color={'inherit'}text={item[1].answers[j].slice(6,-2)} points={!isNaN(item[1].answers[j].slice(-2))?item[1].answers[j].slice(-2):item[1].answers[j].slice(-1)}/>
        ):
        fullytrue<item[0].score&&fullytrue>0?
            prothtml.push(
                <DisplayAnswer type={type} color={'primary'}text={item[1].answers[j].slice(6,-2)} points={!isNaN(item[1].answers[j].slice(-2))?item[1].answers[j].slice(-2):item[1].answers[j].slice(-1)}/>
        ):
            prothtml.push(
                <DisplayAnswer type={type} color={'error'}text={item[1].answers[j].slice(6,-2)} points={!isNaN(item[1].answers[j].slice(-2))?item[1].answers[j].slice(-2):item[1].answers[j].slice(-1)}/>
        )}}) 
    
   return (
    <>
        <Paper className={classes.root}>
            <Typography variant="h4" component="h2" gutterBottom>
                {formDHN[0]}
            </Typography>
            <Typography variant="h5" component="h2" gutterBottom>
                {formDHN[1]}
            </Typography>
            <Typography variant='h5' component="h2">
          {type=='test'?`Your Form grade is:${isNaN(formDHN[3][formDHN[3].length-4][1])?"Survey":formDHN[3][formDHN[3].length-4][1]}`:'Survey'}   
            </Typography>
          {type=='test'?  
            <Typography variant='h6' component="h2">
                Wrong answer/s displayed red.<br></br>
                partly true displayed in blue color.<br></br>
                true answer/s displayed in black.
            </Typography>:<div></div>}
        </Paper>
    <>
      {prothtml}
    </>
       <Link to='/home'><button className='but'>To Home Page</button></Link>
    </>)}
export default FormDisplay