import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Title from '../../Title';
import { Link } from 'react-router-dom';
import { useContext } from 'react';
import { FormsContext } from '../../ContextCreators/FormsContextCreator';
import { Button } from '@material-ui/core';
function getavg(row){
        let createdgrades=0
  for (let i=0;i<row.finished.length;i++){
         createdgrades=createdgrades+row.finished[i].grade}
         let avg=isNaN(createdgrades/row.finished.length)?0:(createdgrades/row.finished.length).toFixed(2)
         return avg}
const useStyles = makeStyles((theme) => ({
     typography: {
    padding: theme.spacing(2),
  },
  seeMore: {
    marginTop: theme.spacing(3),
  },
}));
export default function Orders({forms}) {
  const {setFormD} = useContext(FormsContext)
  const classes = useStyles();
  function handleFormD(row){
    setFormD(row)}
  return (
    <React.Fragment>
        <Title>To Do!</Title>
        <Table padding='none' size="small">
            <TableHead>
                 <TableRow>
                    <TableCell>Dead Line</TableCell>
                    <TableCell>Name</TableCell>
                    <TableCell>Avg Grade</TableCell>
                    <TableCell align="right">To Do</TableCell>
                </TableRow>
            </TableHead>
            <TableBody>
                {forms.map((row) => (
                    <TableRow key={row._id}>
                        <TableCell>{row.deadline?row.deadline.slice(0,10):'no deadline'}</TableCell>
                        <TableCell>{row.formName}</TableCell>
                        <TableCell>{row.formType=='test'?getavg(row):"Survey."}</TableCell>
                        <TableCell align="right"><Link to='/takeform'><Button size='small' variant="contained" color="primary" onClick={()=>handleFormD(row)}>
                        TO DO
                        </Button></Link></TableCell>
                    </TableRow>))}
            </TableBody>
        </Table>
        <div className={classes.seeMore}>
            <Link  to="/TestCreation" >
                Create a New Form!
            </Link>
        </div>
        </React.Fragment>
  );
}