import {Paper , Typography} from '@material-ui/core'
import { makeStyles } from '@material-ui/core';
const useStyles = makeStyles({
 
   question:{
    width:'100%',
    padding:'10px'},
  
 
});
function QuestionProt({name,description,score,type}){
    const classes = useStyles();

return (    
    <Paper className={classes.question}>
       <Typography component="h2" variant="h5" gutterBottom>{name}</Typography>
       <Typography component="h2" variant="h6" gutterBottom>{description}</Typography>
      {type=='test'?<Typography component="h2" variant="h6" gutterBottom>Score: {score}</Typography>:<></>} 
       <Typography component="h2" variant="h6" gutterBottom>Your Answer/s</Typography>
    </Paper>
)}
export default QuestionProt