import { useContext, useEffect,useState } from "react"
import { FormsContext } from "../ContextCreators/FormsContextCreator"
import { UserContext } from "../ContextCreators/UserContextCreator"
import {request as axios}  from '../Axios'
import { groupBy } from "../ProtFunc"
import {Link } from 'react-router-dom'
import {DisclosureContent} from 'reakit'
import EditFormDetailes from './EditFormDetailes'
import EditQuestion from "./EditQuestion"
import EditAnswer from "./EditAnswer"
function EditForm(){
    const {testid} = useContext(FormsContext)
    const {userD} = useContext(UserContext)
    const [editedform,setEditedForm] = useState()
    const [visible, setVisible] = useState(false);
    const [sendto,setSendTo] = useState()
    const [sentlist,setSentList] = useState([])
    const [loading,setLoading] = useState(true)
    let htmlitems=[]
    const questions=[]
    const answers=[]
    const obj=[]
    let allreadysent;
    function handleSendList(data){
    setSentList([...sentlist,data])}
    function handleSendListDel(index){
    sentlist.splice(index,1)
    setSentList([sentlist])} 
    async function handleEdit(e){
    e.preventDefault()
    handleSendList(editedform.needsToDo)
    obj.push(['creator',userD._id],['creatorname',userD.userName],['formType',editedform.formType],['needsToDo',sentlist],['finished',editedform.finished],['open',editedform.open],['_id', editedform._id],[e.target[0].name,e.target[0].value],[e.target[1].name,e.target[1].value],[e.target[2].name,e.target[2].value])
    for (let i=3;i<e.target.length;i++){
        if (e.target[i].name=='text'){        
            answers.push(Object.fromEntries([['Q_id',e.target[i].attributes.data.value],[e.target[i].name,e.target[i].value],[e.target[i+1].name,e.target[i+1].value]]))}
        if (e.target[i].name=='name'){
            questions.push(Object.fromEntries([['Q_id',e.target[i].attributes.data.value],[e.target[i].name,e.target[i].value],[e.target[i+1].name,e.target[i+1].value],[e.target[i+2].name,e.target[i+2].value]]))}
        else{continue}}
    obj.push(['answers',answers],['questions',questions])
    try{ axios.post('/editformsave',[{'_id':testid},Object.fromEntries(obj)])
        setVisible(!visible)}
    catch(error){console.error(error)}} 
    useEffect(async ()=>{
        try{
                let res = await axios.post('/editformdisplay',{'_id':testid})
                allreadysent=res.data.needsToDo?res.data.needsToDo:[]
                setEditedForm(res.data)
                setLoading(false)}
        catch(error){console.error(error)}
    },[])
    if(!loading){
        let concated = editedform.questions.concat(editedform.answers)
        let grouped = groupBy(concated,'Q_id')
        const groupedLen = Object.values(grouped)
        for (let i=0;i<groupedLen.length;i++){
            groupedLen[i].map((item)=>{
                item.name?
                htmlitems.push(
                    <EditQuestion type={editedform.formType} Q_id={item.Q_id} name={item.name}score={item.score}description={item.description}/>
                    ):
                htmlitems.push(
                    <EditAnswer type={editedform.formType} Q_id={item.Q_id}text={item.text}points={item.points}/>)})}}
    if(allreadysent){
        allreadysent.map((item)=><h5>{item}</h5>)}
    
    return (
        loading?
        <div>loading...</div>:
        <>
           <h3>sent to:</h3>
                {allreadysent}
           <h3>send now:</h3>
                <div>{sentlist.map((item,index)=><span><h5>{item}   <button onClick={()=>handleSendListDel(index)} className='ctable1'>-</button></h5></span>)}</div>
            <input type='text'onChange={(e)=>setSendTo(e.target.value)} value={sendto} name='sender'></input>
            <button onClick={()=>handleSendList(sendto)}>+</button>
            <form name='editor' onSubmit={(e)=>handleEdit(e)}>
                <EditFormDetailes name={editedform.formName}description={editedform.formDescription}deadline={editedform.deadline?editedform.deadline.slice(0,10):null}/>  
                {htmlitems}
                <input data='0' type='submit' ></input>
            </form>
            <Link to='/home'>
                <DisclosureContent visible={visible}>
                    <button className='but'>Done</button>
                </DisclosureContent>
            </Link>
        </>)}
export default EditForm