import React from 'react';
import clsx from 'clsx';
import { Link } from 'react-router-dom';
import { Button } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import MenuIcon from '@material-ui/icons/Menu';
import { TextField } from '@material-ui/core';
import { useState } from 'react';
import {request as axios}  from '../Axios'
import { useContext } from 'react';
import { FormsContext } from '../ContextCreators/FormsContextCreator';
import { UserContext } from '../ContextCreators/UserContextCreator';
import { Popover } from '@material-ui/core';
import {Paper} from '@material-ui/core'
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import SearchIcon from '@material-ui/icons/Search'
import NotificationsIcon from '@material-ui/icons/Notifications';
import { mainListItems, UnLoggedMenu } from './listItems';
const drawerWidth = '20vw';
const useStyles = makeStyles((theme) => ({
  
  toolbarIcon: {
    display: 'flex',
    justifyContent: 'flex-end',
    padding: '0px 0px',
    ...theme.mixins.toolbar,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    minWidth:`calc(100%-170px)`,
    width: `calc(100% - ${drawerWidth})`, 
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight:0,
  },
    menuButtonHidden: {
    display: 'inline',
  },  
  title: {
    textAlign:'left',
    flexGrow: 1,
  },
    paper: {
        padding:'8px',

        display: 'flex',
        overflow: 'contain',
        flexDirection: 'column',
        height:'auto',
        width:240
        
    },
    
  drawerPaper: {
    position: 'relative',
    height:'auto',
    width: drawerWidth,
    minWidth:'170px', 
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: { 
    display:'hidden',
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing(0),
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(0),
    },
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: '50vh',
    overflow: 'contain',
  },
   search:{
   width:'13vw',
   boxShadow: '0 3px 5px 2px rgba(255, 200, 5, .2)',
   borderRadius:7,
    }, 
}));
function debounce(callback, wait,event) {
        let timeout;
        return (...args) => {
             clearTimeout(timeout);
         timeout = setTimeout(function () { callback.apply(this, args,event); }, wait);};}

export default function Header() {
  const classes = useStyles();
  const {todoforms ,handleFetch ,setFormD} =useContext(FormsContext)
  const {userD} = useContext(UserContext)
  const [searcher,setSearcer]=useState('')
  const [open, setOpen] = React.useState(false);
  const [anchorEle, setAnchorEle] = React.useState(null);
  const [anchorEle1, setAnchorEle1] = React.useState(null);
  const [searchitems,setSearchItems] = useState([])     
  const handleClick = (event) => {
    handleFetch(userD._id,userD.Email)
    setAnchorEle(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEle(null);
  };
  const opener = Boolean(anchorEle);
  const id = opener ? 'simple-popover' : undefined;
  const handleDrawerOpen = () => {
    if (userD._id){
    handleFetch(userD._id,userD.Email)}
    setOpen(true);
  };
  const handleDrawerClose = () => {
    setOpen(false);
  };
 
  const handleClose1 = () => {
    setSearchItems([])
    setAnchorEle1(null);
  };
  const opener1 = Boolean(searchitems.length>0);
  const id1 = opener1 ? 'simple-popover' : undefined;
  function handleFormDHS(row){
    setFormD(row)}

   async function handleSearch(e){  
       setSearcer(e.target.value)
       setAnchorEle1(e.currentTarget);
     try{
          await axios.post('/fdisplay',{'filter':searcher})
            .then((res)=>
            setSearchItems(res.data))
            }
            catch(error){console.error(error)}}
    
  return (
    <>
        <AppBar position="absolute" className={clsx(classes.appBar, open && classes.appBarShift)}>
            <Toolbar className={classes.toolbar}>
                    <IconButton
                    edge="start"
                    color="inherit"
                    aria-label="open drawer"
                    onClick={handleDrawerOpen}
                    className={clsx(classes.menuButton, open && classes.menuButtonHidden)}
                    >
                            <MenuIcon />
                    </IconButton>
                    <Typography component="h1" variant="h6" color="inherit" /* noWrap */ className={classes.title}>
                        FormBook 
                    </Typography>
                    <div className='search'>
                        <SearchIcon className='spanner'/>
                            <TextField id="standard-basic" disabled={userD._id?false:true} aria-describedby={id1}  onKeyUp={debounce(e=>handleSearch(e),2000)}  className={classes.search}/>
                                <Popover
                                id={id1}
                                open={opener1}
                                anchorEl={anchorEle1}
                                onClose={handleClose1}
                                anchorReference="anchorPosition"
                                anchorPosition={{ top: 65, left: 590 }}
                                anchorOrigin={{
                                vertical: 'top',
                                horizontal: 'left',}}
                                transformOrigin={{
                                vertical: 'top',
                                horizontal: 'left'}}
                                >
                                    {searchitems.map((form)=>
                                            <Paper className={classes.paper}>
                                                        <span>form name:{form.formName}  , form type:{form.formType}</span>
                                                        <Link to='/takeform'>
                                                            <Button size='small' variant="contained" color="primary" onClick={handleFormDHS(form)}>
                                                                to Test
                                                            </Button>
                                                        </Link>
                                            </Paper>)}
                                        </Popover>
                                    </div>
                <IconButton color="inherit">
                    <Badge badgeContent={todoforms?todoforms.length:0} color="secondary">
                        <div>
                            <NotificationsIcon onClick={handleClick} aria-describedby={id}/>
                                <Popover
                                id={id}
                                open={opener}
                                anchorEl={anchorEle}
                                onClose={handleClose}
                                anchorOrigin={{
                                    vertical: 'bottom',
                                    horizontal: 'center',}}
                                transformOrigin={{
                                    vertical: 'top',
                                    horizontal: 'center',}}>
                                        <Typography className={classes.typography}>{todoforms.length>=1?todoforms.map((form)=>
                                            <Paper className={classes.paper}><span>form name:{form.formName}  , type:{form.formType}</span>
                                                <Link to='/takeform'>
                                                    <Button variant="contained" color="primary" onClick={handleFormDHS(form)}>
                                                        to Test
                                                    </Button>
                                                </Link>
                                            </Paper>):
                                            <Paper className={classes.paper}>
                                        {userD._id?<span>'Great! You finished all your quizs.'</span>:<Link to ='/login'><button>Click To Log IN!</button></Link>}
                                            </Paper>}
                                        </Typography>
                                    </Popover>
                                </div>
                            </Badge>
                        </IconButton>
                    </Toolbar>
                </AppBar>
                <Drawer
                variant="temporary"
                classes={{
                    paper: clsx(classes.drawerPaper, !open && classes.drawerPaperClose),}}
                    open={open}> 
                    <div className={classes.toolbarIcon}>
                        <IconButton onClick={handleDrawerClose}>
                            <ChevronLeftIcon />
                        </IconButton>
                    </div>
                    <Divider />
                        <List>{userD._id?mainListItems:UnLoggedMenu}</List> 
                    <Divider />
                </Drawer>
            <div className={classes.appBarSpacer} />
        </>);}
