import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import { Typography } from '@material-ui/core';
import { Button } from '@material-ui/core';
import { Link } from 'react-router-dom';
import LogOutButton from '../../LogOutButton';
import { FormsContext } from '../../ContextCreators/FormsContextCreator';
import { useContext } from 'react';
import Orders from '../Orders';
const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },
    bpaper:{
    display:'flex',
    padding: theme.spacing(3), 
    justifyContent:'space-around'
    },
  cpaper:{
    padding: theme.spacing(1),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
    },
  fixedHeight: {
    height: 'auto',
  },
}));

export default function LoggedDashboard() {
  const classes = useStyles();
  const {totalNum,totalCreated,todoforms} = useContext(FormsContext)
  const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);
  return (<>
        <Grid container spacing={3}>
            <Grid item xs={12} md={12} lg={12}>
                <Paper className={classes.cpaper}>
                    <b>User Stats:</b><br/>This User created {totalCreated} forms so far <br></br>
                    and participated in {totalNum} forms so far,keep browsing our site for more detailed stats<br></br>
                    You can create and publish your own survey or test using our app.<br/>  
                </Paper>
            </Grid>
        <Grid item xs={12} md={12} lg={12}>
                <Paper className={classes.cpaper}>
                    <Orders forms={todoforms}/>  
                </Paper>
            </Grid>
        <Grid item xs={12} md={12} lg={12}>
            <Paper className={fixedHeightPaper}>
                <Typography>
                    <b>About This App:</b><br/>Responsive FormBook web App created with <br></br>
                    React.js state managment using only react hooks and context! 
                     <br/> Express.js ,Material Ui , Mongoose<br></br>
                    You are more then welcome to find bugs and contact me<br/>  
                    <Link color="inherit" href='linkedin.com/in/hen-moshe-6a0373215'>
                        Hen Moshe
                    </Link> :)
                </Typography>
            </Paper>
        </Grid>
            <Grid item xs={12}md={12} lg={12}> 
                <Paper className={classes.bpaper}>
                    <LogOutButton />
                    <Link to='/TestCreation'><Button variant='contained' size='large'>Create Form</Button></Link>
                </Paper>
            </Grid>
        </Grid>

  </>
  );
}
