function EditQuestion({Q_id,name,description,score,type}){

return(
    <>
                <h3>Question:</h3>
               <h4>Question Name:</h4>
    <textarea data={Q_id} type='text' defaultValue={name} name='name'></textarea>
               <h4>Question Description:</h4>
    <textarea data={Q_id} type='text' defaultValue={description} name='description'></textarea>
                    <label for='score'>Question Score: {score}</label>
    <input data={Q_id} type='number' min='0' max='100' step='5' defaultValue={score} name='score' disabled={type==='survey'?true:false}/>
   
            <h3>Answers:</h3>       
                </>
)
}
export default EditQuestion