function EditAnswer({Q_id,text,points,type}){
return (
    <>
            <h3>Answer: </h3>
    <textarea data={Q_id} defaultValue={text} name='text' type='text' ></textarea>
            <h3>Answer Score: {points}</h3>
    <input data={Q_id} defaultValue={points}min='0' max='100' step='5' name='points' type='number'disabled={type==='survey'?true:false}/>
    </>
)
}
export default EditAnswer