import {Paper,Typography} from '@material-ui/core'
import {makeStyles} from '@material-ui/core'
const useStyles = makeStyles({
    answer:{
    margin:'20px',
    width:'90%',
    padding:'5px'}
 
});
function DisplayAnswer({color,text,points,type}){
    const classes = useStyles();

return (
        <Paper className={classes.answer}>
            <Typography component="h2" color={color} variant="subtitle1" gutterBottom>
                {text} <br></br>{type=='test'?`Answer Score: ${points}`:<></>} 
            </Typography>
        </Paper>
)
}
export default DisplayAnswer