import Box from '@mui/material/Box';
import Collapse from '@mui/material/Collapse';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import { useState } from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import { Link } from 'react-router-dom';
import { Button } from '@material-ui/core';
function getavg(row){
        let createdgrades=0
  for (let i=0;i<row.finished.length;i++){
         createdgrades=createdgrades+row.finished[i].grade}
         let avg=isNaN(createdgrades/row.finished.length)?0:(createdgrades/row.finished.length).toFixed(2)
         return avg}  
function Row(props) {
  const { row , setTestId ,setFormDHN } = props;
  const [open, setOpen] =useState(false);
    function fixRowD(row,index){
        let rowd=[row.formName,row.formDescription,row.questions,Object.entries(row.finished[index])]
        setFormDHN(rowd)
        }
  return (
        <>
        <TableRow sx={{ '& > *': { borderBottom: 'unset', } }}>
        <TableCell>
          <IconButton
            aria-label="expand row"
            size="small"
            onClick={() => setOpen(!open)}
          >
            {open ? <button className='ctable1'></button> : <button className='ctable'></button>}
          </IconButton>
        </TableCell>
        <TableCell component="th" scope="row">
          {row.formName}
        </TableCell>
        <TableCell>{getavg(row)}</TableCell>
        <TableCell>{typeof(row.deadline)==='string'?row.deadline.slice(0,10):"No DeadLine"}</TableCell>
        <TableCell align="right">
            <Link to='/editform'>
                <Button size='small'  variant="contained" color="primary" onClick={()=>setTestId(row._id)}>
                      Edit
                </Button>
            </Link>
            </TableCell>
      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={5}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box sx={{ margin: 0 }}>
              <Typography variant="h6" gutterBottom component="div">
               Detailes
              </Typography>
              <Table size="small"padding='none' aria-label="purchases">
                <TableHead>
                  <TableRow>
                    <TableCell>{'Done date'}</TableCell>
                    <TableCell>{'Tested User'}</TableCell>
                    <TableCell>{'Grade'}</TableCell>
                    <TableCell align="right">More</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
        {row.finished.map((historyRow,index) => (
                    <TableRow key={historyRow.tested+row.formName}>
                      <TableCell component="th" scope="row">
                        {typeof(historyRow.donedate)==='string'?historyRow.donedate.slice(0,10):'No date'}
                      </TableCell>
                      <TableCell align='center'>{historyRow.username}</TableCell>
                      <TableCell align='center'>{historyRow.grade}</TableCell>
                      <TableCell align="right">
                           <Link to='/formdisplay'>
                                <Button size='small' variant="contained" color="primary" onClick={()=>fixRowD(row,index)}>
                                    Display
                                </Button>
                            </Link>
                      </TableCell>
                    </TableRow>
                  ))
                }
                </TableBody>
              </Table>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </>)}
export default Row
