import './App.css';
import UnLoggedPage from './UnLoggedPage'
import { UserContext } from './ContextCreators/UserContextCreator';
import { useContext} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Switch, Route } from 'react-router-dom';
import LoggedDashboard from './NavItems/LoggedDashboard';
import TestCreation from './CreateQuestion/TestCreation'
import CreatedForms from './NavItems/CreatedForms';
import DoneForms from './NavItems/DoneForms';
import { Container } from '@material-ui/core';
import Header from './Header';
import { Box } from '@material-ui/core';
import Copyrights from './CopyRights';
import CreateQuestion from './CreateQuestion';
import FormProt from './DoForm';
import FormDisplay from './FormDisplay';
import EditForm from './FormEdit';
import UserDetailes from './NavItems/UserDetailes';
import SurveyStats from './SurveyStats';
import {useHistory} from 'react-router-dom'



const useStyles = makeStyles((theme) => ({
   root: {
    display: 'flex',
    justifyContent:'center'
      },
  content: {
    flexGrow: 1,
    maxWidth:'980px',
    height: '100vh',
    overflow: 'auto',
  },
  container: {
    marginTop: 45,
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
 
}))
function App() {
  const classes = useStyles();
  const { userLogged } = useContext(UserContext)
  const history = useHistory()
  window.onload = (e) =>{history.push('/')} 
  return (
     !userLogged ? <UnLoggedPage /> : 
      <div className={classes.root}>
          <Header />
        <main className={classes.content}>
          <Container maxWidth="lg" className={classes.container}>
            <Switch>
              <Route path='/home' exact component={LoggedDashboard} />
              <Route path='/doneforms' component={DoneForms} />
              <Route path='/TestCreation' component={TestCreation} />
              <Route path='/createdForms' component={CreatedForms} />
              <Route path='/QuestionCreation' component={CreateQuestion} />
              <Route path='/takeform'  component={FormProt}/>
              <Route path='/formdisplay' component={FormDisplay}/>
              <Route path='/editform' component={EditForm}/>
              <Route path='/userdetailes' component={UserDetailes}/>
              <Route path='/surveystats' component={SurveyStats}/>
            </Switch>
            <Box pt={4}>
              <Copyrights />
            </Box>
          </Container>
        </main>

      </div>);
}

export default App;
