import {useContext,useState} from 'react'
import { UserContext } from '../../ContextCreators/UserContextCreator'
import {request as axios}  from '../../Axios'
import { makeStyles } from "@material-ui/core";
import { Paper } from '@material-ui/core'
const useStyles = makeStyles((theme) => ({
    paper: {
        padding: theme.spacing(5),
        display: 'flex',
        overflow: 'auto',
        flexDirection: 'column',
      }}))
function UserDetailes(){
    const classes = useStyles();
    const {userD} =useContext(UserContext)
    const [username,setUserName] = useState(userD.userName)
    const [email,setEmail] = useState(userD.Email)
    const [password,setPassword]= useState()
    async function handleSubmit(e){
        e.preventDefault()
        try{
            await axios.put('/changeuserd',[{'_id':userD._id},{'userName':username,'Email':email,'password':password}])} 
            catch(e){console.error(e)}
        }
    return(<Paper className={classes.paper}>
            <h2>Update user detailes in this page:</h2>
            <h4>click in the grey box to change</h4>
            <h3>User Detailes</h3>
            <label for='username' >User Name:</label>
            <input name='username' type='text'onChange={e=>setUserName(e.target.value)} defaultValue={userD.userName}value={username}></input>
            <label for='email'>E-mail:</label>
            <input name='email'type='email'onChange={e=>setEmail(e.target.value)}value={email} defaultValue={userD.Email}></input>
            <label for='password'>Password:</label>
            <input name='password' type='password' onChange={e=>setPassword(e.target.value)} value={password}></input>
            <button onClick={(e)=>handleSubmit(e)}>Update</button>
        </Paper>)}
export default UserDetailes