import Chart from "../Chart";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core";
import { Grid } from "@material-ui/core";
import { Paper } from "@material-ui/core";
import { FormsContext } from "../../ContextCreators/FormsContextCreator";
import { useContext } from "react";
import CollapsibleTable from "../CollapseTable";
import DenseTable from "../DenseTable";
const useStyles = makeStyles((theme) => ({
  paper: {
   /*  padding: theme.spacing(1), */
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },
  fixedHeight: {
    height: '300px',
    width:'100%'
  },
}));

function CreatedForms() {
    const classes = useStyles();
    const {createdforms} = useContext(FormsContext)
    const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);
    var data=[]
    const proto=createdforms.filter((x)=>x.formType==='survey')
    const proto2=createdforms.filter((x)=>x.formType==='test')
    for (let i=0;i<proto2.length;i++){
        data.push(proto2[i].creationDate.slice(8,10)+'-'+proto2[i].creationDate.slice(5,7))
        let avg=0
        for (let j=0;j<proto2[i].finished.length;j++){
            avg=avg+proto2[i].finished[j].grade}
        data.push(avg/proto2[i].finished.length)}
  return <Grid container spacing={1}>
            <Grid item xs={12} md={12} lg={12}>
                <Paper className={fixedHeightPaper}>
                    <Chart data={data}/>
                </Paper>
            </Grid>
            <Grid item xs={12} md={6} lg={6}>
                 <Paper className={classes.paper}>
                      <CollapsibleTable forms={proto2}/>
                </Paper>
            </Grid>
            <Grid item xs={12} md={6} lg={6}>
                    <Paper className={classes.paper}>
                        <DenseTable forms={proto}/>
                    </Paper>
                </Grid>
            </Grid>
}
export default CreatedForms