import Title from "../../../Title"
import { Typography } from "@material-ui/core"
import { useEffect , useState } from 'react'
import {request as axios}  from '../../../Axios'

function HomePageOne(){
    const [count,setCount] = useState()
    const [usercount,setUserCount] = useState()
        useEffect( async ()=>{
            let res = await axios.post('/sitestats')
                    setCount(res.data) 
            let res2 = await axios.post('/userstats')
                    setUserCount(res2.data)
            },[])
                
        return (
        <>
            <Title>Hello Guest!</Title>
            <Title>Forms Created so far: {count} </Title>
            <Title>Users Registered: {usercount}</Title>
                <Typography>example User:<br/>Email : henmoshe23@gmail.com      ,<br/> Password:  1234    </Typography>
                <Typography variant="h5" color='textSecondary'>
                    You can easely create surveys test and more in our webSite!
                </Typography>
                <Typography variant="h6" color='textSecondary'>
                    then you can easly accses yourself\your class records and statistics.
                </Typography>
        </>
    )
}
export default HomePageOne