import {useContext} from 'react'
import { FormsContext } from "../ContextCreators/FormsContextCreator";
import {UserContext} from '../ContextCreators/UserContextCreator'
import { Link } from 'react-router-dom';
import CheckBoxGroup from './CheckBox';
import {groupBy} from '../ProtFunc'
import {useState} from 'react'
import { DisclosureContent } from "reakit";
import {request as axios}  from '../Axios'

function FormProt ()  {
    const {formD , setFormDHN , handleFetch} = useContext(FormsContext)
    const {userD} = useContext(UserContext)
    const [visible, setVisible] = useState(false);
    const [showText, setShowText] = useState(false);
    const onC = () => setShowText(true);
    const reducer = (accumulator, currentValue) => accumulator + currentValue;
    const questions = formD.questions
    const answers = formD.answers
    const data = questions.concat(answers)
    let groupedQuestions = groupBy(data,'Q_id')
    let groupedLen = Object.values(groupedQuestions)
    let maybe=[]
    let qgrade=[]
    let [canswers,setAnswers] = useState([])
    function passform(){
    setFormDHN([formD.formName,formD.formDescription,questions,canswers])
    }
    let Button = () =><Link to='/formdisplay'><button>Discover result!</button></Link>
    async function handleForm(){
        try{
            if(canswers.length!==groupedLen.length){
                setVisible(!visible)
                throw new Error('there are questions unanswered')}
        else{
            passform()
            onC()
            for(let i=0;i<canswers.length;i++){
                for (let j=0;j<canswers[i][1].length;j++){
                    if (!isNaN(canswers[i][1][j].slice(-2))){
                        qgrade.push(Number(canswers[i][1][j].slice(-2)))}
                    else {
                        qgrade.push(Number(canswers[i][1][j].slice(-1)))}}}
                        canswers.push(['grade',qgrade.reduce(reducer)],['tested',userD._id],['username',userD.userName],['donedate',Date.now()])
            let finished=Object.fromEntries(canswers)
            const sendingdata = [userD.Email,formD._id,finished,'finished']
            await axios.post('/adddone',sendingdata)}
            await handleFetch(userD._id,userD.Email)}
        catch(error){
            console.error(error)}}
        for(let i=0;i<groupedLen.length;i++){
            maybe.push(<CheckBoxGroup canswers={canswers} type={formD.formType} setAnswers={setAnswers} quest={groupedLen[i]}/>)}
    return (
           <>
                <h1>{formD.formName}</h1>
                <h2>{formD.formDescription}</h2>
                    {maybe}  
                <button onClick={handleForm}>Send Test!</button>
                <DisclosureContent visible={visible}>You Didnt Answer all questions.</DisclosureContent>
                {showText ? <Button /> : null}
           </>
  );
}
export default FormProt
 