import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { MenuItem } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import { Grid, Paper } from '@material-ui/core';
import clsx from 'clsx';
import { Link } from 'react-router-dom';
import Title from '../Title';
import { Button } from '@material-ui/core';
import SaveIcon from '@material-ui/icons/Save';
import { useContext, useState } from 'react';
import { FormsContext } from '../ContextCreators/FormsContextCreator';
import {request as axios}  from '../Axios'
import CreateAnswer from './CreateAnswer';


const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',

    },
    absolute: {
        position: 'relative',
        bottom: theme.spacing(1),
        left: theme.spacing(10),
    },
    paper: {
        padding: theme.spacing(0),
        display: 'flex',
        overflow: 'auto',
        margin:'8px',
        flexDirection: 'column',
    },
    paperes: {
        padding: theme.spacing(0),
        display: 'flex',
        overflow: 'auto',
        padding:'10px',
        margin:'8px',
        flexDirection: 'row',
        justifyContent:'space-evenly'
    },
    fixedHeight: {
        height: 'auto',
        width:'85vw'
    },
    inp: {
        marginTop: 7,
    },
    button:{
    marginBottom:12,
    }
}));
function UniqId() {
    return Math.random().toString(16).slice(-4);
}
function CreateQuestion() {
    const classes = useStyles();
    const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);
    const [qscore, setScore] = useState(0)
    const [qname, setName] = useState('')
    const [qdescription, setDescription] = useState('')
    const [must, setMust] = useState('')
    const [sent,setSent] = useState(false)
    const [htmlitems,setHtmlItems] = useState([])
    const { formtype,setFormItems,setQascore,setAnswerPoints,answerpoints,setQuestionScore,questionscore,setQuestionId,testid,qascore,formItems } = useContext(FormsContext)
    const id = { "_id": testid }
    let Q_uniq;
    function newQuestion(e)  { 
        e.preventDefault()
        setHtmlItems([])
        setSent(false)
        setQascore(0)
        setFormItems(0)
        document.getElementById('Cquestion').reset();} 
    function addAnswer(e){
        e.preventDefault()
        setHtmlItems([...htmlitems, <CreateAnswer key={htmlitems.length}/>])}
    async function HandleQuestion() {
        try {
            Q_uniq = UniqId()
            const data = [id,'questions',{ "Q_id": Q_uniq, "name": qname, "description": qdescription, "score": qscore,  'most': must }]
            await axios.post('/qacreate', data)                
            setQuestionId(Q_uniq)
            setSent(true)
            setFormItems(formItems+Number(qscore))
            setQuestionScore(questionscore+Number(qscore))} 
        catch (error) {
            console.error(error)}}
    async function HandleSubmit(){
        try{
            if (questionscore==100&&answerpoints==100||questionscore==0&&answerpoints==0){
                setFormItems(0)
                setQascore(0)
                setQuestionScore(0)
                setAnswerPoints(0)}
            else{
                throw new Error('Not a Valid Score!')}}
        catch(e){console.error(e)}}
    const ConditionalLink = ({ children, to, condition }) => (!!condition && to)
     ? <Link to={to}>{children}</Link>
      : <>{children}</>;
        return (<>
                    <Paper className={fixedHeightPaper}>
                        <form id='Cquestion' className={classes.root} noValidate autoComplete="off">
                            <Title>Create question!</Title>
                                {formtype==='test'?
                                    <h3>Total Questions Score:<br></br>{questionscore} from 100</h3>
                                        :<h2>Form type:{formtype}</h2>}
                            <TextField onChange={e => setName(e.target.value)} className={classes.inp} label="Question Name" variant="outlined" required disabled={sent}/>
                            <TextField onChange={e => setDescription(e.target.value)} className={classes.inp} label="Question Description" variant="outlined" disabled={sent}/>
                            <TextField onChange={e => setMust(e.target.value)} className={classes.inp} id="select2" label="Bonus?" value={must} select disabled={sent}>
                                <MenuItem value="true">Yes</MenuItem>
                                <MenuItem value="false">No</MenuItem>
                            </TextField>
                            <label for="points">Score:{qscore}</label>
                            <input onChange={e=>setScore(e.target.value)}value={qscore} type="range" id="range" name="points" min="0" max="100" step="5"disabled={formtype==='survey'||sent?true:false}/>
                            <Button
                                variant="contained"
                                color="primary"
                                size="small"
                                className={classes.button}
                                startIcon={<SaveIcon />}
                                onClick={HandleQuestion}
                                disabled={sent}>
                                    Save Question!
                            </Button> 
                            <Title>
                                Create Answers!
                            </Title>
                            {formtype==='test'?<h3>Total Answers Score:<br>
                               </br>{qascore} from {formItems}</h3>:<h3>Number of answers:{htmlitems.length}</h3>}
                        </form>
                        </Paper>
                            <Grid container rowSpacing={1} columnSpacing={{ xs:6, sm: 2, md: 3 }}>
                                {htmlitems}
                            <Grid item xs={12} >
                                <Paper className={classes.paperes}>
                                    <button className='create' onClick={(e)=>addAnswer(e)}>create new answer</button>
                                    <button className='create' onClick={(e)=>newQuestion(e)}>create new question</button>
                                </Paper>    
                            </Grid>
                            <Grid item xs={12} >
                                <Paper className={classes.paperes}>
                                    <ConditionalLink to="/editform" condition={questionscore===answerpoints}>
                                        <Button
                                            variant="contained"
                                            color="primary"
                                            size="small"
                                            className={classes.button}
                                            startIcon={<SaveIcon />}
                                            onClick={HandleSubmit}>
                                            Save Test!
                                        </Button>
                                    </ConditionalLink>
                                </Paper>
                            </Grid>
                        </Grid>
                    </>)}
export default CreateQuestion
