import SocialButtons from "../SocialButtons";
import { useState } from "react";
import { Link } from "react-router-dom";
import { DisclosureContent } from "reakit";
import {request as axios}  from '../../Axios'
   
function SignUp(){
    const [name,setName] = useState()
    const [email,setEmail] = useState()
    const [password,setPassword] = useState()
    const [visible, setVisible] = useState(false);
    let [errors,setErrors]=useState()

    async function handleSubmit(e){
        e.preventDefault()
        errors=''
        try{
		const data = {"userName":name,"password":password,"Email":email}
		    let res =await axios.post('/signup',data)
            if (res.data=='missing details!'){
            setErrors(res.data)
            }
            if (res.data=='email allready exists'){
            setErrors(res.data)
            }
            if (!errors){
            setErrors('User Created!! now log in.')
            setVisible(true)}}
        catch(error){
          setErrors(error)
          setVisible(true)
          console.error(error)}
		}
    return (
    <form >
        <h1>Create Account</h1>
        <Link to='login'><h4>To Login</h4></Link>
        <SocialButtons />
        <span>or use your email for registration</span>
        <input onChange={e=>setName(e.target.value)} name="userName" type="text" placeholder="Name" required/>
        <input onChange={e=>setEmail(e.target.value)} name="Email" type="email" placeholder="Email" required/>
        <input onChange={e=>setPassword(e.target.value)}name="password" type="password" placeholder="Password" required/>
        <button onClick={handleSubmit}>Sign Up</button>
        <DisclosureContent visible={visible}>{errors}</DisclosureContent>

    </form>
)}
export default SignUp