import {
  unstable_useFormState as useFormState,
  unstable_Form as Form,
  unstable_FormLabel as FormLabel,
  unstable_FormCheckbox as FormCheckbox,
  unstable_FormGroup as FormGroup,
  unstable_FormSubmitButton as FormSubmitButton,
  unstable_FormMessage as FormMessage,
} from "reakit/Form";
import { DisclosureContent } from "reakit";
import {useState}from 'react'

function CheckBoxGroup({canswers,setAnswers,quest,type}) {
  const question=quest.map((x)=>x)
  let qname=quest[0].name
  const [visible, setVisible] = useState(false);
  let qscore=quest[0].score
  let desc=quest[0].description
  let spliced=question.splice(1,)
  let newarr=[]
  let mistakeallowed=spliced.filter(i=>i.points!='0').length
  const form = useFormState({
    values: {
    abc: [],
    },
    onValidate: (values) => {
    if (type!=='survey'){
        const errors = {};
        if (mistakeallowed!=spliced.length){
            if (values.abc.length<mistakeallowed) {
                errors.abc = `you must choose atleast ${mistakeallowed} value/s!`;}
        if (values.abc.length>mistakeallowed) {
            errors.abc = `you must choose only ${mistakeallowed} value/s!`;}} 
        if (Object.keys(errors).length) {
            throw errors;}}},
    onSubmit: (values) => {
        const errors2 = {};
        let cnt=0
        for(let i=0;i<canswers.length;i++){
            if(canswers[i][0].indexOf(values.abc[0].slice(0,4))===-1){
                continue}
            else{cnt++}}
            if (cnt===0){
                setVisible(!visible)
                setAnswers([...canswers,[values.abc[0].slice(0,4),values.abc]])}
            if (cnt!==0){
                errors2.abc='you allready submitted this answer!'}
            if (Object.keys(errors2).length){
                throw errors2}},});
    spliced.map((item)=>{
        newarr.push(<label  className='checker' >
                   {item.text}
                        <FormCheckbox  {...form} name='abc' value={item.Q_id+qscore+item.text+item.points} />    
                    </label>)})
  return (
    <Form {...form} >
        <FormLabel {...form}name='abc'>
             <span className='pp'>{qname}</span>
             <h3>Question Score:{qscore}</h3>
             <h3>Correct Answers:{mistakeallowed==spliced.length?0:mistakeallowed}</h3>
             <h3>{desc}</h3>
        </FormLabel>
        <FormGroup {...form} name='abc' className='checker3'>         
            <FormLabel {...form} as="legend" name='abc'>
                Answers
            </FormLabel>
            {newarr}
        </FormGroup>
        <FormMessage {...form} name='abc' />
            <FormSubmitButton {...form}>
                Send Question
            </FormSubmitButton>
            <DisclosureContent visible={visible}>
                Answers sent.
            </DisclosureContent>
    </Form>);}
export default CheckBoxGroup