import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import { Button } from '@material-ui/core';
import Paper from '@mui/material/Paper';
import Title from '../../Title';
import { FormsContext } from '../../ContextCreators/FormsContextCreator';
import { useContext } from 'react';
import { Link } from 'react-router-dom';


export default function DenseTable({forms}) {
    const {setSurveyStats} = useContext(FormsContext)

  return (
    <TableContainer component={Paper}>
      <Title>Survey Statistics</Title>
      <Table size="small" padding='none' aria-label="a dense table">
        <TableHead>
          <TableRow>
            <TableCell>Survey Name</TableCell>
            <TableCell align="right">Participets</TableCell>
            <TableCell align="right">Show stats</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
        {forms.map((row) => (
            <TableRow
              key={row.formName}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row">
                {row.formName}
              </TableCell>
              <TableCell align='center'>{row.finished.length}</TableCell>
              <TableCell align="right">
                        <Link to='/surveystats'>
                            <Button size='small' variant="contained" color="primary" onClick={()=>setSurveyStats(row)}>
                                Display
                            </Button>
                        </Link>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}