import React from 'react'
import { FormsContext } from '../FormsContextCreator'
import { useState } from 'react'
import {request as axios}  from '../../Axios'

function FormsProvider({ children }) {
  let [createdforms,setCreatedForms] = useState([])
  let [todoforms,setToDoForms] = useState([])
  let [doneforms,setDoneForms] = useState([])
  let [testid, setTestId] = useState('')
  let [questionid, setQuestionId] = useState('')
  let [formItems,setFormItems] = useState(0)
  const [formD,setFormD] = useState()
  let [formDHS,setFormDHS]= useState()
  let [formDHN,setFormDHN]= useState([])
  let [formtype,setFormType] = useState('')
  let [questionscore,setQuestionScore] = useState(0)
  let [answerpoints,setAnswerPoints] = useState(0)
  let [qascore,setQascore] = useState(0)
  let [surveystats,setSurveyStats] = useState()
  
    async function fetchCreatedForms(token) {
    try {
      let res = await axios.post('/myfdisplay',{creator:token})
      setCreatedForms(res.data)
    }
    catch (error) { console.error(error) }
  }


    async function fetchToDoForms(token) {
    try {
       let res = await axios.post('/myfdisplay',{'needsToDo':token}) 
           setToDoForms(res.data)    
    }
    catch (error) { console.error(error) }
  } 
   async function fetchDoneForms(token) {
    try {
      let res = await axios.post('/doneuserfdisplay',{finished:token})
          setDoneForms(res.data)
    }
    catch (error) { console.error(error) }
  }
     
    async function handleFetch(token,email){
    await fetchToDoForms(email)
    await fetchCreatedForms(token)
    await fetchDoneForms(token)
    }
    const totalNum = todoforms.length+doneforms.length
    const totalCreated = createdforms.length
  return <FormsContext.Provider value={{surveystats,setSurveyStats, setFormType,formtype,totalNum,totalCreated,qascore,setQascore,handleFetch,setFormItems,formDHN,setFormDHN,formDHS,setFormDHS,answerpoints,setAnswerPoints,setQuestionScore,questionscore,setFormD,formD,formItems,doneforms,todoforms,createdforms,  setTestId, testid, setQuestionId, questionid }}>
    {children}
  </FormsContext.Provider>
}
export default FormsProvider